@import '../abstracts/variables';
@import '../base/animations';

body {
	font-family: 'Lato', sans-serif;
	font-weight: 400;
	font-size: $default_font_size;
	line-height: 1.7;
	color: $color_grey_dark;
}

.heading-primary {
	color: $color_white;
	text-transform: uppercase;
	backface-visibility: hidden;
	margin-bottom: 6rem;

	&--main {
		display: block;
		font-size: 6rem;
		font-weight: 400;
		letter-spacing: 3.5rem;

		animation-name: moveInLeft;
		animation-duration: 1s;
		animation-timing-function: ease-out;
		/* animation-iteration-count:3;
  animation-delay:3s; */

		@include respond(phone) {
			letter-spacing: 1rem;
			font-size: 5rem;
		}
	}

	&--sub {
		display: block;
		font-size: 2rem;
		font-weight: 700;
		letter-spacing: 1.74rem;

		animation-name: moveInRight;
		animation-duration: 1s;
		animation-timing-function: ease-out;

		@include respond(phone) {
			letter-spacing: 0.5rem;
		}
	}
}

.heading-secondary {
	font-size: 3.5rem;
	text-transform: uppercase;
	font-weight: 700;
	display: inline-block;
	background-image: linear-gradient(
		to right,
		$color_primary_light,
		$color_primary_dark
	);
	-webkit-background-clip: text;
	color: transparent;
	letter-spacing: 0.2rem;
	transition: all 0.2s;

	@include respond(tab-port) {
		font-size: 3rem;
	}

	@include respond(phone) {
		font-size: 2.5rem;
	}

	&:hover {
		transform: skewY(2deg) skewX(15deg) scale(1.1) translateY(-1rem);
		text-shadow: 0.5rem 1rem 2rem rgba($color_black, 0.2);
	}
}

.heading-tertiary {
	font-size: $default_font_size;
	font-weight: 700;
	text-transform: uppercase;
}

.paragraph {
	font-size: $default_font_size;

	&:not(:last-child) {
		margin-bottom: 3rem;
	}
}
